'use client';

import { useEffect, useState } from 'react';
import css from './FilterBar.module.css';
import Select, { StylesConfig } from 'react-select';
import { getBrands } from '@/lib/api';
import { useCarListStore } from '@/store/useCarListStore';

interface BrandOption {
  value: string;
  // label: string;
}

const customStyles: StylesConfig<BrandOption> = {
  control: base => ({
    ...base,
    backgroundColor: 'var(--inputs)',
    borderRadius: '12px',
    border: 'none',
    padding: '6px 6px',
    width: '204px',
    fontFamily: 'var(--font-family)',
    // scrollbarColor: 'var(--gray-light)',
  }),
  placeholder: base => ({
    ...base,
    color: 'var(--main)',
  }),
  singleValue: base => ({
    ...base,
    color: 'var(--main)',
  }),
  option: (base, { isFocused, isSelected }) => ({
    ...base,
    // backgroundColor: isSelected ? '#fff' : isFocused ? '#f2f2f2' : '#fff',
    color: isSelected ? 'var(--main)' : 'var(--gray)',
    cursor: 'pointer',
  }),
};

// interface FilterBarProps {
//   onChange: (value: string) => void;
// }

const FilterBar = () => {
  const { setBrand, fetchCars } = useCarListStore();
  const [brandOptions, setBrandOptions] = useState<BrandOption[]>([]);

  useEffect(() => {
    const fetchOptions = async () => {
      try {
        const brands = await getBrands();
        const formattedBrands = brands.map(brand => ({ value: brand, label: brand }));
        setBrandOptions(formattedBrands);
      } catch (error) {
        console.error('Failed to fetch brands', error);
        // !delete console.log
      }
    };
    fetchOptions();
  }, []);

  const handleSearch = () => {
    fetchCars(true);
  };

  return (
    <div className={css.allFilters}>
      <Select<BrandOption>
        options={brandOptions}
        styles={customStyles}
        placeholder="Choose a brand"
        onChange={option => setBrand('brand', option?.value)}
        isSearchable
        instanceId="brand-select"
      />

      {/* <div className={css.carMileageInput}>
        <input></input>
        <input></input>
      </div> */}

      <button className={css.searchBtn} onClick={handleSearch}>
        Search
      </button>
    </div>
  );
};

export default FilterBar;
